$primaryColor: #4527a4;

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

// Skype Button
.wp-block-sbb-skype-button {
	* {
		box-sizing: border-box;
	}

	.sbbSkypeButton {
		display: inline-block;

		a,
		.dropdown {
			font-size: inherit;
			display: inline-flex;
			align-items: center;
			gap: 6px;
			cursor: pointer;

			span {
				line-height: 98%;
			}
		}

		a {
			text-decoration: none !important;
		}

		.dropdown {
			position: relative;

			&:hover {
				height: 100%;
			}

			ul {
				position: absolute;
				left: 0;
				top: calc(100% + 10px);

				list-style-type: none;
				width: 150px;
				color: #00aff0;
				background: #fff;
				display: none;
				flex-direction: column;
				gap: 20px;
				padding: 10px 20px;
				margin: 0;
				border: 2px solid rgb(0, 175, 240);
				transition: all .3s ease;

				li {
					line-height: 100%;
				}

				span {
					color: #00aff0 !important;
				}
			}

			ul.show {
				display: flex;
			}
		}
	}
}